@media only screen and (max-width: 1925px) {
  .cartCountNum {
    position: relative;
    bottom: 10px !important;
  }
  
}
.cart-items {
  display: flex;
  position: relative;
  right: 3vw;
}
.cart {
  color: #3066BE !important;
  font-size: 2rem;
  padding: 1rem;
  bottom: 5px;
  position: relative;
}

.cartCount {
  right: 0vw;
  position: absolute;
  color: white;
  font-family: sans-serif;
  z-index: 1;
  background-color: #119DA4;
  height: 1.5vh;
  border-radius: 50%;
  padding: 0.3rem;
}

.cartCountNum {
  position: relative;
  bottom: 5px;
}

.cart:hover {
  color: #ffffff !important;
}

.cart-items:hover {
  cursor: pointer;
}

.cart-arrow {
  position: absolute !important;
  right: 3.5vw !important;
  top: 7vh !important;
  border-left: 30px solid transparent !important;
  border-right: 30px solid transparent !important;
  border-bottom: 50px solid #edede9 !important;
}

.modal-cart {
  position: absolute;
  right: 3vw;
  background-color: #edede9;
  top: 10vh;
  height: auto;
  width: 20vw;
  z-index: 1;
  border-radius: 5%;
  scrollbar-color: #888 #f2f2f2;
}
.modal-cart:hover {
  scrollbar-color: #555 #f2f2f2;
}

.table-container {
  max-height: 350px;
  overflow-y: auto;
}

.table {
  margin-bottom: 0;
}

.cart-image {
  height: auto;
  width: auto;
}

.btn-container {
  background: #e5e5e5;
  display: flex;
  flex-direction: column;
}

.modal-cart::-webkit-scrollbar {
  width: 8px;
}

.modal-cart::-webkit-scrollbar-track {
  background: #f2f2f2;
}

.modal-cart::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 4px;
}

.modal-cart::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

.no-items {
  padding: 1em;
  font-weight: 600;
  font-size: 1em;
  font-family: "poppins-small", sans-serif;
  letter-spacing: 1px;
}

.cart-trash {
  color: #8d99ae;
  font-size: 1.3em;
  position: relative;
  top: 5px;
  padding-right: 0.5em;
}
.cart-trash:hover {
  cursor: pointer;
  color: #ef233c;
}
